body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  display: flex;
}

.editor,
.markdown,
.preview {
  flex: 1;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

.markdown {
  outline: none;
  resize: none;
  border: 1px solid #eaecef;
  border-top: 0;
  border-bottom: 0;
}
